/* animation */

@keyframes anvil {
    0% {
      transform: scale(1) translateY(0px);
      opacity: 0;
      box-shadow: 0 0 0 rgba(241, 241, 241, 0);
    }
    1% {
      transform: scale(0.96) translateY(10px);
      opacity: 0;
      box-shadow: 0 0 0 rgba(241, 241, 241, 0);
      
    }
    100% {
      transform: scale(1) translateY(0px);
      opacity: 1;
      box-shadow: 0 0 500px rgba(241, 241, 241, 0);
      
    }
  }

  
.popup-content {
    -webkit-animation: anvil 0.3s cubic-bezier(0.38, 0.1, 0.36, 0.9) forwards;
    margin: auto;
    -webkit-overflow-scrolling: touch;
    background: rgb(255, 255, 255);
    width: 70%;
    height: 70%;
    padding: 5px;
    
  }
  .popup-arrow {
    color: rgb(255, 255, 255);
  }
  [role='tooltip'].popup-content {
    width: 200px;
    
  }
  
  .popup-overlay {
    background: rgba(0, 0, 0, 0.313);
  }
  [data-popup='tooltip'].popup-overlay {
    filter: blur(8px);
  }

  .modal {
    background-color: white;
    padding:3%;
    font-size: 12px;
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
  }
  
  .modal > .header {
    width: 100%;
    border-bottom: 2px solid #be95ff;
    font-size: 24px;
    text-align: center;
    font-weight: bold;
    padding: 5px;
  }
  .modal > .content {
    width: 100%;
    padding: 10px 5px;
    font-size: 20px;
  }


  .modal > .actions {
    width: 100%;
    padding: 10px 5px;
    margin: auto;
    text-align: center;
    
  }
  .modal > .close {
    cursor: pointer;
    position: absolute;
    display: block;
    padding: 2px 5px;
    line-height: 20px;
    right: 3px;
    top: 2px;
    font-size: 24px;
    background: #ffffff;
    border-radius: 18px;
    border: 2px solid #cfcece;
  }